<template>
  <section class="sign-in-page">
    <div id="container-inside">
      <div id="circle-small"></div>
      <div id="circle-medium"></div>
      <div id="circle-large"></div>
      <div id="circle-xlarge"></div>
      <div id="circle-xxlarge"></div>
    </div>
    <div class="container p-0">
      <div class="row no-gutters">
        <div class="col-md-6 text-center pt-5">
          <div class="sign-in-detail text-white"></div>
        </div>
        <div class="col-sm-6 bg-white pt-2">
          <div class="sign-in-from">
            <h1 class="mb-0">انشاء حساب</h1>
            <form class="mt-2" @submit.prevent="onSubmit()">
              <div class="form-group">
                <label for="exampleInputEmail1">الاسم</label>
                <input
                  type="text"
                  v-model="v$.form.name.$model"
                  class="form-control mb-0"
                  id="exampleInputEmail1"
                  placeholder="ادخال الاسم هنا"
                />
                <small style="color: red" v-if="v$.form.name.$error"
                  >الرجاء قم بادخال اسمك كما هو في المستند الرسمي</small
                >
              </div>
                  <div class="form-group">
                <label for="exampleInputEmail1">الاسم</label>
                <input
                  type="text"
                  v-model="v$.form.name.$model"
                  class="form-control mb-0"
                  id="exampleInputEmail1"
                  placeholder="ادخال الاسم كما هو في فيس بوك هنا"
                />
                <small style="color: red" v-if="v$.form.name.$error"
                  >الرجاء قم بادخال اسمك كما هو في المستند الرسمي</small
                >
              </div>
              <div class="form-group">
                <label for="exampleInputEmail2">الايميل</label>
                <input
                  v-model="v$.form.email.$model"
                  type="email"
                  class="form-control mb-0"
                  id="exampleInputEmail2"
                  placeholder="ادخال الايميل هنا"
                />
                <small style="color: red" v-if="v$.form.email.$error"
                  >الرجاء قم بادخال بريدك الاكتروني</small
                >
              </div>
              <div class="form-group">
                <label for="exampleInputPassword1">الرمز السري</label>
                <input
                  v-model="v$.form.password.$model"
                  type="password"
                  class="form-control mb-0"
                  id="exampleInputPassword1"
                  placeholder="ادخال ارمز السري هنا"
                />
                <small style="color: red" v-if="v$.form.password.$error"
                  >الرجاء قم بادخال كلمة السر</small
                >
              </div>
              <div class="form-group">
                <label class="form-label" for="customFile"
                  >صورة مستند تثبت هويتك  </label
                >
                <input
                  class="form-control"
                  type="file"
                  id="customFile"
                  ref="file"
                  @change="uploadFile"
                  multiple
                />
                <small style="color: red" v-if="v$.form.file.$error"
                  >الرجاء قم برفع صورة او صورتين</small
                >
              </div>

              <div class="d-inline-block w-100">
                <button type="submit" class="btn btn-primary float-end">
                  انشاء حساب
                </button>
              </div>

              <div class="sign-info">
                <span class="dark-color d-inline-block line-height-2">
                  تملك حساب؟<router-link :to="{ name: 'auth1.sign-in1' }">
                    تسجيل الدخول
                  </router-link></span
                >
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import useVuelidate from "@vuelidate/core";
import { required, email, minLength ,requiredIf} from "@vuelidate/validators";

export default {
  name: "SignUp",
  setup() {
    return { v$: useVuelidate() };
  },

  data() {
    return {
      options: {
        centeredSlides: false,
        loop: false,
        slidesPerView: 1,
        autoplay: {
          delay: 3000,
        },
        spaceBetween: 15,
        pagination: {
          el: ".swiper-pagination",
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },

        // And if we need scrollbar
        scrollbar: {
          el: ".swiper-scrollbar",
        },
      },
      form: {
        name: "",
        password: "",
        email: "",
        file: [],
      },
    };
  },
  methods: {
    uploadFile() {
      
      this.form.file = this.$refs.file.files;
    },
    async onSubmit() {
      this.v$.$touch();
      if (!this.v$.form.$invalid) { 
       const res = await this.$store
          .dispatch("register", {
            email: this.form.email,
            password: this.form.password,
            name: this.form.name,
            file:this.form.file,
        
          }) 
        this.$router.push({ name: 'social.book' })
      }
    },
  },
  validations() {
    return {
      form: {
        email: {
          required,
          email,
        },
        password: {
          required,
          min: minLength(8),
        },
        name: {
          required,
        },
        file: {
          required: requiredIf(function (nestedModel) {
            return this.form.file.length == 0; // New changes
          }),
        },
      },
    };
  },
};
</script>

